import axios, {BASEURL} from '../../utils/axios'
import {getUserId} from "@/utils/util";


/**
 * 当日饮食界面详情
 * @returns {*}
 */

export function getDayFoodDetial(startDietTimeDesc,endDietTimeDesc) {
  return axios.get(`/monitor/health/eat/syLogFoodHeatHistory/getFoodHeatHistoryDetail`,{ params: {
      startDietTimeDesc, endDietTimeDesc
    }});
}

// 历史数据
export function getFoodHistoryDetail(startDietTimeDesc,endDietTimeDesc) {
    return axios.get(`/monitor/health/eat/syLogFoodHeatHistory/getFoodHistoryDetail`,{ params: {
            startDietTimeDesc, endDietTimeDesc
        }});
}

/**
 * 常见食物列表
 * @param typeId
 * @returns {*}
 */
export function getFoodList(typeId) {
  return axios.get(`/monitor/health/food/foodHeat/getCommonFoodHeatList`);
}

/**
 * 搜索
 * @returns {*}
 */
export function getBuFoodHeatListByName(name) {
  return axios.get(`/monitor/health/food/foodHeat/getBuFoodHeatListByName?name=`+name);
}


// 添加饮食记录
export function insertFoodRecord(dietTimeDesc,timetype,name,heat,nums,unit,foodHeatId,url,day){

    console.log('添加饮水记录',...arguments)
    return axios.post("/monitor/health/eat/syLogFoodHeatHistory/insert",{
        dietTimeDesc,timetype,name,heat,nums,unit,foodHeatId,url,userId: getUserId(),day
    })
}
